A,CACA,wBAAA,CACA,aA9kBQ,CA+kBR,cAAA,CAEA,aACE,aAAA,CACA,iBAAA,CAIJ,qCAEI,aACE,YAAA,CACA,yBACE,cAAA,CACA,iBAAA,CASE,6CACE,mBAAA,CAGE,0EACE,gBAAA,CAIJ,wDACE,eAAA,CAOV,4BACE,mBAAA,CAGI,iGACE,cAAA,CAOV,QACE,eAAA,CAAA,CAIJ,qCAEI,aACE,qBAAA,CAII,qCACE,qBAAA,CASN,yBACE,qBAAA,CAEA,qCACE,cAAA,CAGF,oCACE,gBAAA,CAIN,0BACE,gBAAA,CAIA,4BACE,qBAAA,CACA,oCAAA,CACA,QAAA,CAGE,6CACE,mBAAA,CACA,mBAAA,CAEA,wDACE,eAAA,CAOV,4BACE,qBAAA,CACA,kBAAA,CACA,iDACE,qBAAA,CAIE,iGACE,YAAA,CAIF,iGACE,gBAAA,CAEF,8SAGE,cAAA,CAIN,oDACE,oCAAA,CAAA,CAMR,qCAGM,qBACE,iBAAA,CACA,OAAA,CACA,KAAA,CACA,iBAAA,CAEA,kBAAA,CACA,WAAA,CACA,eAAA,CAEA,kCACE,YAAA,CACA,kBAAA,CACA,QAAA,CACA,QAAA,CAEA,qCACE,SAAA,CACA,uCACE,WAAA,CACA,gBAAA,CAMR,wBACE,YAAA,CAQA,oCACE,eAAA,CACA,aAAA,CAKJ,4BACE,UAAA,CAGE,6CACE,qBAAA,CACA,mBAAA,CAEE,0EACE,gBAAA,CAGJ,wDACE,eAAA,CAQR,gDACE,gCAAA,CACA,eAAA,CACA,YAAA,CACA,sBAAA,CAGE,qGACE,eAAA,CAAA,CAQZ,sCACE,cACE,SAAA,CAIA,aACE,iBAAA,CAGE,oCACE,gBAAA,CAQJ,yBACE,gBAAA,CAEA,qCACE,gBAAA,CAKJ,4BACE,iBAAA,CACA,oCAAA,CAII,2DACE,YAAA,CAOV,4BACE,gBAAA,CACA,qBAAA,CACA,gDACE,8BAAA,CACA,iBAAA,CAEE,iGACE,YAAA,CACA,eAAA,CAIN,6CACE,mBAAA,CAEF,2CACE,kBAAA,CAGF,oDACE,oCAAA,CAAA,CAMR,sCACE,cACE,gBAAA,CAIA,aACE,gBAAA,CACA,gBAAA,CAEA,yBACE,cAAA,CAMI,uCACE,gBAAA,CAUR,uBACE,gBAAA,CAGF,sBACE,gBAAA,CAOF,4BACE,gBAAA,CACA,gBAAA,CACA,aAAA,CAII,2DACE,YAAA,CAMA,0EACE,gBAAA,CAGJ,yDACE,gBAAA,CAEF,wDACE,gBAAA,CAOV,4BACE,gBAAA,CACA,gBAAA,CACA,gDACE,8BAAA,CACA,iBAAA,CAEE,iGACE,YAAA,CACA,eAAA,CAIN,6CACE,mBAAA,CAKE,uFACE,oCAAA,CAAA","sourcesContent":["$font-logo: \"Crimson Text\", serif;\n$my-font: \"Inter\", sans-serif;\n$primary: #fefae0;\n$slate-grey: #f8f8ff;\n$dark-army: #5f6f52;\n$dark-slate: #1a1a1a;\n$slate-army: #a9b388;\n$slate-brown: #b99470;\n$border: 2px solid rgba(211, 211, 211, 0.4);\n$box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.3);\n$box-shadow2: 0px 2px 5px 0px rgba(0, 0, 0, 0.3);\n\n* {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\n\nhtml {\n  scroll-behavior: smooth;\n}\n\nbody {\n  background-color: $slate-grey;\n  font-family: $my-font;\n}\n\n.skip-content {\n  position: absolute;\n  top: -100%;\n  left: 20px;\n  z-index: 1030;\n  text-decoration: none;\n  background-color: $primary;\n  border: 2px solid $slate-brown;\n  border-radius: 5px;\n  color: #000;\n  padding: 0.5rem 1rem;\n  min-width: 44px;\n  display: inline-block;\n  font-weight: 500;\n\n  &:focus {\n    top: 20px;\n    outline: 5px dashed sandybrown;\n  }\n}\n\n#header {\n  width: 100%;\n  margin: 0 auto;\n  position: fixed;\n  background-color: $dark-army;\n  border-block-end: 2px solid $primary;\n  box-shadow: $box-shadow2;\n  z-index: 1000;\n\n  .row {\n    height: fit-content;\n    margin: 0 auto;\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    padding: 1rem 1.5rem;\n\n    .card-logo {\n      display: flex;\n      justify-content: center;\n      align-items: center;\n      gap: 5px;\n\n      .logo-img {\n        display: inline-block;\n        width: 40px;\n        height: 100%;\n      }\n      .title-text {\n        font-size: 2rem;\n        font-weight: 600;\n        color: $primary;\n        font-family: $font-logo;\n        line-height: 2.3rem;\n        margin-right: 1.5rem;\n        cursor: default;\n      }\n    }\n\n    .navbar {\n      visibility: hidden;\n      position: absolute;\n      top: 100%;\n      right: -100%;\n      height: fit-content;\n      width: 100%;\n      background-color: $dark-army;\n      border-block-end: 2px solid $primary;\n      box-shadow: $box-shadow;\n      transition: all 0.2s ease-in-out;\n\n      &.active {\n        visibility: visible;\n        right: 0;\n      }\n\n      .card-navbar {\n        list-style: none;\n        display: flex;\n        flex-direction: column;\n        align-items: center;\n        gap: 1rem;\n        margin: 1rem 0;\n\n        li {\n          width: 100%;\n          text-align: center;\n          padding-inline: 1.5rem;\n\n          a {\n            display: flex;\n            justify-content: center;\n            align-items: center;\n            min-width: 44px;\n            min-height: 44px;\n            text-decoration: none;\n            color: $primary;\n            font-weight: 500;\n            padding: 0.5rem 0;\n            border-radius: 8px;\n            transition: all 0.1s ease-in-out;\n\n            &:hover {\n              background-color: $primary;\n              color: $dark-army;\n            }\n\n            &:focus {\n              background-color: $primary;\n              color: $dark-army;\n            }\n          }\n        }\n      }\n    }\n\n    #hamburger {\n      display: flex;\n      align-items: center;\n      justify-content: center;\n\n      .hamburger-menu {\n        min-width: 44px;\n        min-height: 44px;\n        background-color: transparent;\n        font-size: 2.2rem;\n        color: $primary;\n        cursor: pointer;\n        border: 1px solid $primary;\n        border-radius: 5px;\n        transition: all 0.2s ease-in-out;\n\n        &:hover {\n          background-color: $primary;\n          color: $dark-army;\n        }\n\n        i {\n          display: block;\n        }\n      }\n    }\n  }\n}\n\n#main-content {\n  background-color: whitesmoke;\n  padding-bottom: 2.5rem;\n  min-height: 100vh;\n\n  #hero {\n    width: 100%;\n    min-height: 400px;\n    height: 100vh;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    background-image: url(\"../public/images/heros/hero-image_2.jpg\");\n    background-size: cover;\n    background-position: center;\n    background-repeat: no-repeat;\n    position: relative;\n\n    .row {\n      padding: 1rem 1.5rem;\n      text-align: center;\n      color: $primary;\n      display: grid;\n      gap: 0.5rem;\n      position: absolute;\n      top: 60%;\n      transform: translateY(-60%);\n\n      .hero-title {\n        font-size: 2.8rem;\n        font-weight: 600;\n        font-family: $font-logo;\n        line-height: 2.8rem;\n      }\n\n      .hero-text {\n        font-size: 1.2rem;\n        font-style: italic;\n      }\n    }\n  }\n\n  .title-main {\n    margin: 2rem auto;\n    color: $slate-army;\n    font-size: 0.8rem;\n    text-shadow: 1px 2px 0 $dark-army;\n    text-align: center;\n  }\n\n  .text-favorite__empty {\n    text-align: center;\n    color: $dark-slate;\n    font-style: italic;\n    display: block;\n    margin-top: 4rem;\n  }\n\n  #content {\n    width: 100%;\n    margin: 0 auto;\n    scroll-margin-block-start: 180px;\n\n    .row {\n      padding: 1rem 1.5rem;\n      display: grid;\n      gap: 2rem;\n      grid-template-columns: 1fr;\n\n      .card {\n        position: relative;\n        height: 100%;\n        border-radius: 8px;\n        overflow: hidden;\n        box-shadow: $box-shadow2;\n        background-color: white;\n        margin: 0 auto;\n\n        .card-img {\n          overflow: hidden;\n          .card-img-item {\n            display: inline-block;\n            width: 100%;\n            height: 200px;\n            object-fit: cover;\n            object-position: center;\n            transform: scale(1);\n            transition: all 0.2s ease-in-out;\n          }\n        }\n\n        .card-body {\n          padding-inline: 1.5rem;\n          padding-bottom: 1.5rem;\n          display: grid;\n\n          .card-title  {\n            .card-title-item {\n              display: inline-block;\n              min-width: 44px;\n              min-height: 44px;\n              padding-right: 1rem;\n              padding-block: 10px;\n              font-size: 1.3rem;\n              font-weight: 600;\n              text-transform: capitalize;\n              color: $dark-army;\n              transition: all 0.2s ease-in-out;\n              text-decoration: none;\n            }\n          }\n\n          .card-group {\n            display: flex;\n            justify-content: space-between;\n            align-items: center;\n            font-size: 1rem;\n            // border: 1px solid;\n            padding-bottom: .5rem;\n\n            .card-location {\n              display: flex;\n              align-items: center;\n              gap: 0.2rem;\n\n              .icon {\n                color: rgb(233, 0, 0);\n              }\n\n              .text-location {\n                font-weight: 500;\n                color: #444444;\n              }\n            }\n\n            .card-stars {\n              color: rgb(255, 220, 19);\n\n              .text-star {\n                color: #444444;\n                font-weight: 500;\n              }\n            }\n          }\n\n          .card-desc {\n            font-size: 1rem;\n            font-weight: 300;\n            text-align: justify;\n            cursor: default;\n            display: -webkit-box;\n            -webkit-line-clamp: 5;\n            -webkit-box-orient: vertical;\n            overflow: hidden;\n          }\n        }\n\n        &:hover {\n          .card-title {\n            .card-title-item {\n              text-decoration: underline;\n            }\n          }\n          .card-img {\n            .card-img-item {\n              transform: scale(1.1);\n            }\n          }\n        }\n      }\n    }\n  }\n\n  .detail_resto {\n    width: 100%;\n    margin: 0 auto;\n    padding-top: 5.5rem;\n    padding-inline: 1.5rem;\n    cursor: default;\n\n    .detail_resto__title {\n      font-size: 2rem;\n      text-align: center;\n      padding-bottom: 1rem;\n      color: $slate-army;\n      text-shadow: 1px 2px 0 $dark-army;\n    }\n\n    .detail_resto__card {\n      display: grid;\n      grid-template-columns: 1fr;\n      gap: 0.5rem;\n      overflow: hidden;\n\n      .detail_resto__left-content {\n        .detail_resto__image {\n          display: inline-block;\n          width: 100%;\n          border-radius: 16px;\n          height: 250px;\n          object-fit: cover;\n          object-position: center;\n        }\n      }\n\n      .italic {\n        font-style: italic;\n      }\n\n      .detail_resto__right-content {\n        display: grid;\n        gap: 0.4rem;\n\n        .detail_resto__item {\n          font-size: 1.2rem;\n          color: $dark-slate;\n          font-weight: 600;\n          display: flex;\n          align-items: center;\n          gap: 0.2rem;\n\n          .icon-loc {\n            color: rgb(233, 0, 0);\n          }\n\n          .icon-star {\n            color: rgb(255, 220, 19);\n          }\n        }\n\n        .detail_resto__description {\n          font-style: italic;\n          font-weight: 300;\n          font-size: 0.8rem;\n          text-align: justify;\n          cursor: text;\n        }\n\n        .detail_resto__menu,\n        .detail_resto__category {\n          font-style: italic;\n          font-weight: 600;\n          font-size: 0.8rem;\n\n          span {\n            font-weight: 300;\n          }\n        }\n      }\n    }\n\n    .horizontal-line {\n      width: 100%;\n      height: 1px;\n      background-color: $dark-slate;\n      margin-block: 1.5rem;\n    }\n\n    .remove-margin {\n      margin-block: 0.2rem;\n    }\n\n    .customer_riviews__title {\n      text-align: center;\n      color: $dark-slate;\n      font-family: $font-logo;\n      padding-block-end: 1.5rem;\n    }\n\n    .customer_riviews__card {\n      display: grid;\n      grid-template-columns: repeat(1, 1fr);\n      gap: 1rem;\n\n      .customer_riviews__item {\n        background-color: $slate-grey;\n        border: 2px solid $slate-army;\n        color: $dark-slate;\n        border-radius: 10px;\n        padding-block: 1rem;\n        padding-inline-start: 0.5rem;\n        padding-inline-end: 1rem;\n        display: grid;\n        grid-template-columns: 50px auto;\n        gap: 0.2rem;\n\n        .customer_riviews__item-icons {\n          width: 48px;\n          height: 48px;\n          text-align: center;\n          .icon-user {\n            font-size: 3rem;\n            border-radius: 50%;\n          }\n        }\n\n        .customer_